body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



body {
  margin: 0;
  background-color: whitesmoke;
}

header {
  /*height: 100px;
  display: flex;*/
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  background: #338dff;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #338dff, #0f1964); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

header > img {
  height: 80%;
  margin-left: 10%;
}

header > p {
  font-family: VT323, monospace;
  color: whitesmoke;
  font-size: 50px;
 }


button {
  border: none;
  font-family: VT323, monospace;
  font-size: 2.3em;
  letter-spacing: 1.5px;
  color: rgb(0, 0, 0);
  background: rgb(255, 255, 255);
}

button:active {
  background: #e5e5e5;
  box-shadow: inset 0px 0px 5px #020202;
   outline: none;
}

.calculator > .display {
  width:320px;
  font-size: 2.6em;
  text-align: right;
  margin-bottom:1em
 }

 .calculator  {
    margin: auto;
    text-align: center;
 }

 .inputBoxes {
   width: 320px;
   margin:auto;
   background-color: white;
 }

 .row1 > button, .row2 > button {
  width:16.66%
}

.row3 > button {
  width: 50%
}

.row4 > button {
  width: 100%
}

 .calculator > .history {
  width:320px;
  font-size: 1.6em;
  text-align: left;
  margin-top:1em;
  background-color: lightyellow;
  border: none
 }

 .container {
  margin: auto;
  max-width: 800px;
}

@media (max-width: 425px) {
 
  .history {
    width: 100% !important;
  }
  .inputBoxes {
    width: 100% !important;
  }
  .display {
    width: 100% !important;
  }
}

